import Logo from './components/Logo.vue';

//  install 
// Vue.use() 
const install = function(Vue, opts = {}) {
    // 
    if (install.installed) return;

    //  name
    Vue.component(Logo.name, Logo);
}

//  , <script scr=''></script>
if (typeof window !== 'undefined' && window.Vue) {
    install(window.Vue);
}

// 
module.exports = {
    install,
    Logo
}


