<template>
    <div class="logo">
        {{defaultValue}}
    </div>
</template>

<script>
    export default {
        name: 'logo',
        data() {
            return {
                defaultValue: ''
            }
        }
    }
</script>

<style lang="less" scoped>
    logo{
        width: 50px;
        height: 50px;
        background: url('../img/logo.png') no-repeat 0 0/50px 50px;
    }
</style>


